<template>
  <div>
  <svg id = 'svgE' :style = 'style' >
   
   <g v-show = 'ccShow.indexOf(ccLast4) != -1'>
     <text v-for = '(d,i) in dateList'
     :key = 'i'
     :dy = 'height - margin.y +20'
     :dx = 'axisConfig.xS(i)'
     text-anchor = 'middle'
     fill = 'black'
     >{{d}}
       
     </text>
   </g>
   <g id = 'groupArea' ></g>
   <g id = 'title' v-show = 'ccLast4'>
     <text dx = 10 dy = 25 font-size = 20 font-weight = 'bold'>
       Credit card ID: {{ccLast4}}
     </text>
   </g>
  </svg>
  
  </div>
</template>

<script>
  import * as d3 from 'd3'
export default {
	name: 'viewE',
	data(){
    
    let date = []
    for(let i = 6;i<20;i++){
      if(i<10){
        date.push("01/0"+i)
      }
      else{
        date.push("01/"+i)
      }
    }
    return {
      width:this.$store.state.width *0.6,
      height:this.$store.state.height * 0.4,
      margin :{
              x:60,
              y:30
                },
      area : d3.svg.area()
              .interpolate("basis")
              .x(d=> this.axisConfig.xS(d.x))
              .y1(d=>this.axisConfig.yS(d.y + d.y0))
              .y0(d=>this.axisConfig.yS(d.y0)),
      ccData:[],
      dateList:date,
      hoverLC:null,
      colorTmp:'',
      hoverShop:null,
      
      
      
      
      
      
    }
  },
  mounted(){
    d3.csv("cc_data.csv",d=>{
      this.ccData = d
    })
    
    
    
  },
  computed:{
    maxY(){
      let sol = 0
      
      for(let i = 0;i<this.dateList.length;i++){
        
        for(let j = 0;j<this.stackData.length;j++){
          //console.log((this.rawData[j][i]['y0'] +this.rawData[j][i].y))
          if((this.stackData[j][i].y0 +this.stackData[j][i].y) >sol){
            sol = (this.stackData[j][i].y0 +this.stackData[j][i].y)
          }
        }
      }
      return sol
    },
    rawData(){
      var getSum = (cclast4,shop,date)=>{
        let tmp = 20
        for(let i = 0;i<this.ccData.length;i++){
          
          if(this.ccData[i].last4ccnum == cclast4 && 
          this.ccData[i].location == shop && this.ccData[i].timestamp.substring(0,5) == date){
            tmp += parseFloat(this.ccData[i].price)
            
          }
        }
        return tmp
      }
      let sol = []
      for(let i = 0;i<this.shopList.length;i++){
        let tmp = []
        for(let j = 0;j<this.dateList.length;j++){
          tmp.push({x:j,y:getSum(this.ccLast4,this.shopList[i],this.dateList[j])})
        }
        sol.push(tmp)
      }
      return sol
    },
    shopList(){
      let sol = []
      for(let i = 0;i<this.ccData.length;i++){
        if(this.ccData[i].last4ccnum == this.ccLast4 && sol.indexOf(this.ccData[i].location) == -1){
          sol.push(this.ccData[i].location)
        }
      }
      return sol
    },
    ccLast4(){
      return this.$store.state.viewE.ccLast4
    },
    shopListAll(){
      let shopList = []
      for(let i = 0;i<this.ccData.length;i++){
        if(this.ccShow.indexOf(this.ccData[i].last4ccnum) != -1 && shopList.indexOf(this.ccData[i].location)==-1){
          shopList.push(this.ccData[i].location)
        }
      }
      return shopList
    },
    ccShow(){
      return this.$store.state.viewC.showCCList
    },
    stackData(){
      let stack = d3.layout.stack()
                  .offset("wiggle")
      
               return stack(this.rawData)
    },
    axisConfig(){
      return {
        xScale : d3.scale.linear().domain([0,13]).range([0,this.width - 2 * this.margin.x]),
        xS : d3.scale.linear().domain([0,13]).range([this.margin.x,this.width - this.margin.x]),
        yS : d3.scale.linear().domain([this.maxY,0]).range([this.margin.y,this.height - this.margin.y]),
        color : d3.scale.linear().domain([6,0]).range(["skyblue","blue"]),
        color2 : d3.scale.category10(),
      }
    },
    style(){
      return "position:absolute;border-radius: 15px;top:"+(this.$store.state.height*0.6+40)+"px;border:3px solid #37af70;width:"+this.width+"px;"+"height:"+this.height+ "px;" + "left:"+(this.$store.state.width*0.4+40)+"px"
    }
  },
  methods:{
    removeTip(){
      d3.select("#svgE").select("#Etip").remove()
      
    },
    createTips(d,i){
      let xy = [event.offsetX,event.offsetY]
      if(xy[0]>this.width - 250){
        xy[0] = event.offsetX - 260
      }
      
      if(xy[1]>this.height - 190){
        xy[1] = event.offsetY - 200
      }
      let blockWidth = (this.width - 2 * this.margin.x)/13
      let index = Math.floor((event.offsetX - this.margin.x + blockWidth/2)/blockWidth)
      
      let g = d3.select("#svgE")
      .append("g")
      .attr("id",'Etip')
      .attr("transform","translate("+[xy[0]+10,xy[1]+10]+")")
      
      g.append("rect")
      .attr("fill",'black')
      .attr("fill-opacity",0.7)
      .attr("height",190)
      .attr("width",250)
      .attr("rx",15)
      
      g.append("text")
      .attr("dx",10)
      .attr("dy",30)
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("Credit card id:")
      
      g.append("text")
      .attr("dx",240)
      .attr("dy",50)
      .attr("text-anchor",'end')
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text(this.ccLast4)
      
      g.append("text")
      .attr("dx",10)
      .attr("dy",70)
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("Target location:")
      
      g.append("text")
      .attr("dx",240)
      .attr("dy",90)
      .attr("text-anchor",'end')
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text(this.shopList[i])
      
      
      
      g.append("text")
      .attr("dx",10)
      .attr("dy",110)
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("Date:")
      
      g.append("text")
      .attr("dx",240)
      .attr("dy",130)
      .attr("text-anchor",'end')
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text(this.dateList[index])
      
      g.append("text")
      .attr("dx",10)
      .attr("dy",150)
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("Spending:")
      
      g.append("text")
      .attr("dx",240)
      .attr("dy",170)
      .attr("text-anchor",'end')
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("€ " +(d[index].y - 20).toFixed(2))
      
    },
    log(){
      //console.log(this.maxY,this.stackData,this.rawData)
      this.createStream()
    },
    createStream(){
      let groupArea = d3.select("#svgE").select("#groupArea")
      groupArea.selectAll(".area")
      .data(this.stackData)
      .enter()
      .append("path")
      .attr("class","area")
      .attr("fill-opacity",0)
      .attr("fill",'white')
      .on("mouseover",(d,i)=>{
        this.createTips(d,i)
        d3.select("#Cgroups")
        .selectAll("path")
        .filter(d=>d.index == this.ccShow.indexOf(this.ccLast4))
        
        .attr("stroke",'#fa857e')
        .attr("stroke-width",5)
        
        d3.select("#Cgroups")
        .selectAll("path")
        .filter(d=>d.index == this.shopListAll.indexOf(this.shopList[i])+this.ccShow.length)
        
        .attr("stroke",'#0bbdf1')
        .attr("stroke-width",5)
        
        d3.select("#Cchords")
        .selectAll("path")
        .filter(d=>!(d.source.index == this.ccShow.indexOf(this.ccLast4) && d.target.index== this.shopListAll.indexOf(this.shopList[i])+this.ccShow.length) )
       
        .attr("fill-opacity",0.2)
        .attr("stroke-width",0)
        
        this.colorTmp = d3.select("#Cchords")
        .selectAll("path")
        .filter(d=>d.source.index == this.ccShow.indexOf(this.ccLast4) && d.target.index== this.shopListAll.indexOf(this.shopList[i])+this.ccShow.length )
        .attr("fill")
        
        
        d3.select("#Cchords")
        .selectAll("path")
        .filter(d=>d.source.index == this.ccShow.indexOf(this.ccLast4) && d.target.index== this.shopListAll.indexOf(this.shopList[i])+this.ccShow.length )
        
        .attr("fill-opacity",0.9)
        .attr("stroke-width",0)
        .attr("fill",'#37af70')
        
        this.hoverLC = i
        groupArea.selectAll(".area")
        .transition()
        .attr("fill-opacity",0.3)
        .attr("fill",(_,i)=>this.axisConfig.color(i))
        
        groupArea.selectAll(".area")
        .filter((d,i)=>i == this.hoverLC)
        .transition()
        .attr("fill","#2a61e3")
        .attr("fill-opacity",1)
      })
      .on("mousemove",(d,i)=>{
        this.removeTip()
        this.createTips(d,i)
      })
      .on("mouseout",(d,i)=>{
        
        this.removeTip()
        d3.select("#Cgroups")
        .selectAll("path")
        .attr("stroke",'none')
        
        d3.select("#Cchords")
        .selectAll("path")
        .filter(d=>d.source.index == this.ccShow.indexOf(this.ccLast4) && d.target.index== this.shopListAll.indexOf(this.shopList[i])+this.ccShow.length )
        
        .attr("fill",this.colorTmp)
        
        d3.select("#Cchords")
        .selectAll("path")
        
        .attr("fill-opacity",0.5)
        .attr("stroke-width",1)
        
       
        
        groupArea.selectAll(".area")
        .transition()
        .attr("fill-opacity",0.9)
        .attr("fill",(_,i)=>this.axisConfig.color(i))
      })
     

      groupArea.selectAll(".area")
      .data(this.stackData)
      .transition()
      .duration(500)
      .attr("fill-opacity",0.9)
      .attr("d",d=>this.area(d))
      .attr("fill",(_,i)=>this.axisConfig.color(i))
        
      groupArea.selectAll(".area")
      .data(this.stackData)
      .exit()
      .transition()
      .remove()
        
        
      }
         
    },
    watch:{
      
      ccLast4(nv,ov){
        
        if(nv && ov == 0){
          this.createStream()
          d3.select("#svgE").append("rect")
          .attr("x",this.margin.x - 10)
          .attr("y",30)
          .attr("height",this.height )
          .attr("width",this.width)
          .attr("fill",'white')
          .transition()
          .duration(2000)
          .attr("x",this.width)
          .remove()
        }
        else if (ov && nv == 0){
          d3.select("#svgE").append("rect")
          .attr("x",this.width)
          .attr("y",30)
          .attr("height",this.height )
          .attr("width",this.width)
          .attr("fill",'white')
          .transition()
          .duration(2000)
          .attr("x",0)
          .remove()
          
          d3.select("#svgE").select("#groupArea")
          .selectAll(".area")
          
          .transition()
          .duration(2000)
          .remove()
          
        }
        else{
          this.createStream()
        }
       
      }
    }
  }

</script>

