<template>
  <div>
  <svg id = 'svgB' :style = 'style' >
    <g id = 'map'>
      
      <path class = 'land' v-for = '(d,i) in geoData.land.features'
      :key = 'i'
      fill = 'None'
      stroke = 'black'
      stroke-opacity = 0.7
      stroke-width = '0.5px'
      :d = 'mapPath(d)'
      >
      </path>
      
      
    </g>
    
    <g id = 'gps'>
    </g>
    <g id = 'locations'>
      <template v-for = '(d,i) in placeLocation'
        :key = 'i'>
        <circle @mouseover = 'hoverTip(d)' @mousemove = 'hoverTip(d),hoverShop = d.name' @mouseout = 'tipRemove(),hoverShop = null'
        :cx = "projection(d.location)[0]"
        :cy = "projection(d.location)[1]"
        :r = '7 + d.heat/15'
        :fill = 'd.name == hoverShop? "#3a90ff":"red"'
        :fill-opacity ='d.name == "GAS tech"?0.5:eventsList.indexOf(d.name) == -1? 0.15:0.6'
        >
          
        </circle>
      </template>
    </g>
  
    
    <g v-for = '(d,i) in showID'
    :key = 'i'>
      <g class = 'pos' v-if = 'getPos(d)'
      :transform = '"translate("+[projection(getPos(d))[0] -5+ (showID.indexOf(d)) * 5,projection(getPos(d))[1] - 30]+")"'
      >
        <circle 
        cx = 0
        cy = 0
        r = 15
        :fill = 'color(showID.indexOf(d))'
        >
          
        </circle>
        <polygon points = '13,7.5 -13,7.5 0,30'
        :fill = 'color(showID.indexOf(d))'>
          
        </polygon>
        <circle cx = 0 cy = 0 r = 11 fill = 'white'>
          
        </circle>
        
        <circle cx = 0 cy = 0 r = 5 :fill = 'color(showID.indexOf(d))'>
          
        </circle>
        
        <circle cx = 0 cy = 0 r = 3 fill = 'white'>
          
        </circle>
        <rect x = -12
        y = -1
        width = 8
        height = 2
        :fill = 'color(showID.indexOf(d))'>
          
        </rect>
        <rect x = 4
        y = -1
        width = 8
        height = 2
        :fill = 'color(showID.indexOf(d))'>
          
        </rect>
        
      </g>
      
    </g>
    <g class = 'time'
    :transform = '"translate("+[width-20,30]+")"'>
    <text dy = 0 text-anchor = 'end'
    font-size = 20
    font-weight = 'bold'>Start Time: &nbsp;{{startTime.hour>=10?startTime.hour:'0'+startTime.hour}}:{{startTime.minute>=10?startTime.minute:'0'+startTime.minute}}:{{startTime.second>=10?startTime.second:'0'+startTime.second}}</text>
      <text dy = 30 text-anchor = 'end'
      font-size = 20
      font-weight = 'bold'>Present Time: &nbsp;{{time.hour>=10?time.hour:'0'+time.hour}}:{{time.minute>=10?time.minute:'0'+time.minute}}:{{time.second>=10?time.second:'0'+time.second}}</text>
      <text dy = 60 text-anchor = 'end'
      font-size = 20
      font-weight = 'bold'>End Time: &nbsp;{{endTime.hour>=10?endTime.hour:'0'+endTime.hour}}:{{endTime.minute>=10?endTime.minute:'0'+endTime.minute}}:{{endTime.second>=10?endTime.second:'0'+endTime.second}}</text>
      <text dy = 90 text-anchor = 'end'
      font-size = 20
      font-weight = 'bold'>Time Rate: &nbsp; 1:{{timeRate}}</text>
      
    </g>
    <g id = 'seperate'>
      
      <rect stroke = '#37af70' stroke-width = 3
      :x = 'width - 260' y = -30
      height = 180
      rx = 15
      width = 400
      fill = 'none'>
        
      </rect>
      
    </g>
    <g  cursor = 'pointer' id = 'restart' :transform = '"translate("+[25,25]+")"'
      @mouseover = 'refresh("over")' @mousedown = 'refresh("down")'
      @mouseup = 'refresh("up")' @click = 'refresh("click")'
      @mouseout = 'refresh("out")'>
      <circle style = 'transition: all 0.3s;' @mouseover = 'refresh("over")' cx = 0 cy = 0 r = 20 :fill = 'refreshColor' stroke = 'none'></circle>
      <circle cx = 0 cy = 0 r = 10 fill = 'none' stroke = 'grey' stroke-width = 3></circle>
      <line style = 'transition: all 0.3s;' x1 = 0 x2 = 15 y1 = 0 y2 = 0 :stroke = 'refreshColor' stroke-width = 6></line>
      <polygon points = '3,-3 11,-3 11,-11' fill = 'grey'></polygon>
    </g>
  </svg>
  
  
  </div>
  <!-- <p style = 'position:absolute;left:10px;top:20px'>{{timeFlag}},{{time}},{{getPos('1')}}</p> -->
</template>

<script>
  import * as d3 from 'd3'
export default {
  
	name: 'viewB',
	data(){
    return {
      color :d3.scale.category10(),
      width:this.$store.state.width * 0.5 -20,
      height:this.$store.state.height * 0.6,
      projection:d3.geo.mercator().center([24.875,36.074]).scale(500000 * this.$store.state.width/1800),
      simpleGPS:[],
      geoData:{
        land:{},
        island:{}
      },
      location:{
        
      },
      gps:{
        
      },
      matchData:[],
      placeLocation:[],
      ccData : [],
      hoverFlag :false,
      stingFlag :false,
      hoverPlace:{
        xy:[],
        place:''
      },
      time:{
        hour:0,
        minute:0,
        second:0
      },
      stopData:[],
      timeFlag:false,
      timeMachine:null,
      refreshColor:"white",
      hoverShop:null,
      
    }
  },
  mounted(){
    
    d3.json("simpleGPS.json",d=>{
      this.simpleGPS = d
    })
    d3.json("Abila.json",d=>{
      this.geoData.land = d
    })
    
    d3.json("location.json",d=>{
      this.location = d
    })
    
    d3.json("heatLocation.json",d=>{
      this.placeLocation = d
    })
    
    d3.csv("cc_data.csv",d=>{
      this.ccData = d
    })
    
    d3.csv("match.csv",d=>{
      this.matchData = d
    })
    d3.json("stop.json",d=>{
      this.stopData = d
    })
  },
  computed:{
    shop(){
      return this.$store.state.hoverShop
    },
    timeRate(){
      return this.$store.state.interval
    },
    startTime(){
      return this.$store.state.startTime
    },
    endTime(){
      return this.$store.state.endTime
    },
    interval(){
      return this.$store.state.interval
    },
    
    dict(){
      let sol = {}
      for(let i = 0;i<this.showID.length;i++){
        for(let j = 0;j<this.matchData.length;j++){
          if(this.showID[i] == this.matchData[j].id){
            sol['c'+this.matchData[j].cclast4] = this.showID[i]
            sol['c'+this.matchData[j].sus1] = this.showID[i]
            sol['c'+this.matchData[j].sus2] = this.showID[i]
          }
        }
      }
      return sol
    },
    tsTime(){
      return this.time.hour * 3600 + this.time.minute * 60 + this.time.second
    },
   
    day(){
      return this.$store.state.day
    },
    allEvents(){
      let sol = []
      for(let i = 0;i<this.ccData.length;i++){
        
        if (this.ccData[i].timestamp.substring(0,10) == this.day  && this.ccShow.indexOf(this.ccData[i].last4ccnum) != -1){
          sol.push(this.ccData[i])
        }
      }
      return sol
    },
    pointEventList(){
      let sol = []
      for(let i = 0;i<this.allEvents.length;i++){
        if(this.allEvents[i].location == this.hoverPlace.place){
          sol.push(this.allEvents[i])
        }
      }
      function getTimeJump(a,b){
        let day1 = parseInt(a.substring(3,5))
        let day2 = parseInt(b.substring(3,5))
        let hour1 = parseInt(a.substring(11,13))
        let minute1 = parseInt(a.substring(14,16))
        let sec1 = parseInt(a.substring(17,19))
        let hour2 = parseInt(b.substring(11,13))
        let minute2 = parseInt(b.substring(14,16))
        let sec2 = parseInt(b.substring(17,19))
        
        return (day1 - day2) * 24 * 60 * 60 + (hour1-hour2) * 3600 + (minute1-minute2) * 60 + (sec1-sec2)
      }
      sol.sort((a,b)=>getTimeJump(a.timestamp,b.timestamp))
      return sol
    },
    eventsList(){
      let sol = []
      for(let i = 0;i<this.ccData.length;i++){
        
        if (this.ccData[i].timestamp.substring(0,10) == this.day &&sol.indexOf(this.ccData[i].location) == -1 && this.ccShow.indexOf(this.ccData[i].last4ccnum) != -1){
          sol.push(this.ccData[i].location)
        }
      }
      return sol
    },
    ccShow(){
      return this.$store.state.viewC.showCCList
    },
    showTime(){
      return this.$store.state.viewB.showTime
    },
    showGPS(){
      let sol = []
      for(let i = 0;i<this.showID.length;i++){
        let data = this.simpleGPS['id'+this.showID[i]]
        for(let j = 0;j<data.length;j++){
          /* if(parseInt(data[j][0].substring(12,14))>=this.showTime[0] &&
           parseInt(data[j][0].substring(12,14))<=this.showTime[1]){
             sol.push(data[j])
           } */
            if(this.judgeIfShow(data[j])){
              sol.push(data[j])
            }
        }
      }
      return sol
    },
    showID(){
      return this.$store.state.viewB.showIDList
    },
    mapPath(){
      return d3.geo.path().projection(this.projection)
    },
    style(){
      
      return "background-color:"+"white" +";position:absolute;border-radius: 15px;top:20px;border:3px solid #37af70;width:"+this.width+"px;"+"height:"+this.height+ "px;" + "left:"+(this.width+80)+"px"
    },
    
  },
  methods:{
    refresh(type){
      console.log("~~~")
      if(type == 'over'){
        this.refreshColor = '#d8d9e4'
      }
      else if(type == 'down'){
        this.refreshColor = '#b2b3bf'
      }
      else if(type == 'up'){
        this.refreshColor = '#d8d9e4'
      }
      else if(type == 'click'){
        if(this.showGPS.length>0){
          this.createGps()
        }
      }
      else if(type == 'out'){
        this.refreshColor = 'white'
      }
    },
    getPos(id){
      function getTimeJump(a,b){
        let day1 = parseInt(a.substring(3,5))
        let day2 = parseInt(b.substring(3,5))
        let hour1 = parseInt(a.substring(11,13))
        let minute1 = parseInt(a.substring(14,16))
        let sec1 = parseInt(a.substring(17,19))
        let hour2 = parseInt(b.substring(11,13))
        let minute2 = parseInt(b.substring(14,16))
        let sec2 = parseInt(b.substring(17,19))
        
        return (day1 - day2) * 24 * 60 * 60 + (hour1-hour2) * 3600 + (minute1-minute2) * 60 + (sec1-sec2)
      }
      let h = this.time.hour.toString().length == 1?"0"+this.time.hour.toString():this.time.hour.toString()
      let m = this.time.minute.toString().length == 1?"0"+this.time.minute.toString():this.time.minute.toString()
      let s = this.time.second.toString().length == 1?"0"+this.time.second.toString():this.time.second.toString()
      let data = this.stopData['id'+id]
      let time = this.day + ' ' + h + ':'+m + ':' + s
      
      if(data){
        if(getTimeJump(time,data[0].startTime)<0){
          return data[0].position
        }
        
        else{
          for(let i = 0;i<data.length;i++){
            //console.log(time,data[i].startTime,data[i].endTime)
            //console.log(getTimeJump(time,data[i].startTime),getTimeJump(time,data[i].endTime))
            if(getTimeJump(time,data[i].startTime)>=0 && getTimeJump(time,data[i].endTime)<=0){
              return data[i].position
            }
            else if(getTimeJump(time,data[i].endTime)>=0 && getTimeJump(time,data[i+1].startTime)<=0 && i < data.length-1){
              return data[i].position
            }
          }
          return null
        }
      }
      else{
        return null
      }
      
    },
    hoverTip(d){
      let xy = [event.offsetX,event.offsetY]
      if(xy[0]>this.width - 220){
        xy[0] = xy[0] - 240
      }
      if(xy[1]>this.height - 100){
        xy[1] = xy[1] - 100
      }
     //this.hoverPlace.xy = xy
      /* this.hoverFlag = true
      
      this.hoverPlace.place = d.name */
      if(this.hoverPlace.place != d.name){
         this.hoverPlace.place = d.name
      }
     
      this.tipRemove()
      let g = d3.select("#svgB").append("g").attr("id","tips")
      g.append("rect")
      .attr("rx",10)
      .attr("x",xy[0]+10)
      .attr("y",xy[1]+30)
      .attr("height",50 + 30 * this.pointEventList.length)
      .attr("width",220)
      .attr("fill",'black')
      .attr("fill-opacity",0.7)
      
      g.append('text')
      .attr("dy",xy[1]+30+25)
      .attr("fill",'white')
      .attr("text-anchor",'middle')
      .attr("font-size",14)
      .attr("dx",xy[0]+10+110)
      .attr('font-weight','bold')
      .text(d.name)
      
      
      g.selectAll(".list")
      .data(this.pointEventList)
      .enter()
      .append("text")
      .attr("class","list")
      .attr("dx",20 + xy[0]+10)
      .attr("dy",(d,i)=>50 + 30 * i +xy[1]+30 )
      .attr("fill",'white')
      .attr("font-weight",'bold')
      .text((d,i)=>{
        return (i+1) + '. ' + "Car id: " + this.dict['c'+d.last4ccnum]
        +', ' +"Time: " +d.timestamp.substring(11,16)
      })
      
      
    },
    tipRemove(){
      d3.select("#svgB").select("#tips").remove()
    },
    
    add1(){
        let t = this.tsTime
        let last = this.endTime.hour * 3600 + this.endTime.minute * 60 + this.endTime.second
        if(t<=last - this.timeRate/10){
          
          t+=this.timeRate/10
          let h = Math.floor(t/3600)
          let m = Math.floor((t-h*3600)/60)
          let s = t-3600 * h - 60 * m
          
          this.time.hour = h
          this.time.minute = m
          this.time.second = s
        }
        else if(t<last+1){
          t = last 
          let h = Math.floor(t/3600)
          let m = Math.floor((t-h*3600)/60)
          let s = t-3600 * h - 60 * m
          
          this.time.hour = h
          this.time.minute = m
          this.time.second = s
        }
        else{
          this.timeFlag = false
        }
        
      
    },
    createGps(){
      this.timeFlag = true
      this.time.hour = this.startTime.hour
      this.time.minute = this.startTime.minute
      this.time.second = this.startTime.second
      function getTimeJump(a,b){
        let day1 = parseInt(a.substring(3,5))
        let day2 = parseInt(b.substring(3,5))
        let hour1 = parseInt(a.substring(11,13))
        let minute1 = parseInt(a.substring(14,16))
        let sec1 = parseInt(a.substring(17,19))
        let hour2 = parseInt(b.substring(11,13))
        let minute2 = parseInt(b.substring(14,16))
        let sec2 = parseInt(b.substring(17,19))
        
        return (day1 - day2) * 24 * 60 * 60 + (hour1-hour2) * 3600 + (minute1-minute2) * 60 + (sec1-sec2)
      }
      function count(id,data){
        let c = 0
        for(let i = 0;i<data.length;i++){
          if(data[i][1] == id){
            c++
          }
        }
        
        return c
      }
      //console.log(this.showGPS)
      if(this.showGPS.length>0){
        let groupGps = d3.select("#svgB").select("#gps")
        
        
        let sol = []
        
        for(let i = 0;i<this.showID.length;i++){
          sol.push(count(this.showID[i],this.showGPS))
        }
        
        
        
        
        let min 
        let lst = []
        //console.log(sol)
        if(sol.length == 0){
          lst =[]
          min = ''
        }
        
        else if(sol.length == 1){
          if(sol[0]){
            lst.push(this.showGPS[0][0])
          }
          
        }
        else if(sol.length == 2){
          lst.push(this.showGPS[0][0])
          if(sol[1]){
            lst.push(this.showGPS[sol[0]][0])
          }
        }
        else if(sol.length == 3){
          lst.push(this.showGPS[0][0])
          if(sol[1]){
            lst.push(this.showGPS[sol[0]][0])
          }
          if(sol[2]){
            lst.push(this.showGPS[sol[0]+sol[1]][0])
          }
          
        }
        lst.sort((a,b)=>getTimeJump(a,b))
             
        if(lst[0]){
          min = lst[0]
          this.time.hour = parseInt(min.substring(11,13))
          this.time.minute = parseInt(min.substring(14,16))
          this.time.second = parseInt(min.substring(17,19))
          
          
          groupGps.selectAll(".gpsCircle")
          .remove()
          
          groupGps.selectAll(".gpsCircle")
          .data(this.showGPS)
          .enter()
          .append("circle")
          .attr("class","gpsCircle")
          .attr("r",0)
          .attr("fill",d=>{
            
            return this.color(this.showID.indexOf(d[1].toString()))
            })
          .attr("fill-opacity",0.4)
          
          
          groupGps.selectAll(".gpsCircle")
          .data(this.showGPS)
          .transition()
          
          .delay(d=>getTimeJump(d[0],min) * 100 / (this.timeRate/10)  )
          .attr("cx",d=>this.projection([d[3],d[2]])[0])
          .attr("cy",d=>this.projection([d[3],d[2]])[1])
          
          
          
          .attr("r",7)
          .transition()
          .duration(500)
          .attr("r",4)
          .transition()
          .duration(1000)
          .remove()
          
          groupGps.selectAll(".gpsCircle")
          .data(this.showGPS)
          .exit()
          .transition()
          .remove()
        }
      }

    },
    log(){
      this.createGps()
      //console.log(this.showGPS)
    },
    judgeIfShow(d){
      let h = parseInt(d[0].substring(11,13))
      let m = parseInt(d[0].substring(14,16))
      let s = parseInt(d[0].substring(17,19))
      
      if(d[0].substring(0,10) == this.day && 
      h * 3600 + m * 60 + s >= this.startTime.hour * 3600 +this.startTime.minute * 60 + this.startTime.second &&
      h * 3600 + m * 60 + s <= this.endTime.hour * 3600 +this.endTime.minute * 60 + this.endTime.second){
        return true
      }
      return false
    },
    alertName(d){
      alert(d.name)
    },
    transformTwoPoint(location,type){
      let x1 = this.projection(location[0])[0],
      y1 = this.projection(location[0])[1]
      //x2 = this.projection(location[1])[0],
      //y2 = this.projection(location[1])[1]
      
      if (type == 'x'){
        return x1+5
      }
      else if(type == 'y'){
        return y1+5
      }
      else if(type == 'width'){
        //return 50*(x2-x1)
        return 20
      }
      else if(type == 'height'){
        //return 50*(y2-y1)
        return 20
      }
      
    },
    distance(a,b){
      let ax = a[0] /180 * Math.PI,
      ay = a[1]/180 * Math.PI,
      bx = b[0]/180 * Math.PI,
      by = b[1]/180 * Math.PI
      
      return 6370 * Math.acos((Math.sin(ay)*Math.sin(by) + Math.cos(ay) * Math.cos(by) * Math.cos(bx-ax)))
      
    },
    
    trans(d){
      let scaleX = d3.scale.linear().domain([0,685]).range([24.82396,24.91025]),
      scaleY = d3.scale.linear().domain([0,383.75]).range([36.09572,36.04527])
      
      return [scaleX(d[0]),scaleY(d[1])]
    }
  },
  watch:{
    hoverShop(nv){
      this.$store.commit({
        type:'changeHoverShop',
        shop:nv
      })
    },
    showGPS(){
      this.createGps()
    },
    timeFlag(val){
      if(val){
        this.timeMachine = setInterval(this.add1,100)
      }
      else{
        clearInterval(this.timeMachine)
        this.timeMachine = null
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
