<template>
  <div>
  <svg id = 'svgC' :style = 'style' >
   
  </svg>
  </div>
</template>

<script>
  import * as d3 from 'd3jsv5'
  import * as d4 from 'd3'
export default {
	name: 'viewD',
	data(){
    
    return {
      colors:d3.schemeSet3 ,
      width:this.$store.state.width *0.4,
      height:this.$store.state.height * 0.65,
      outerR: Math.min(this.$store.state.width *0.4, this.$store.state.height * 0.65) / 2 - 100,
      innerR: Math.min(this.$store.state.width *0.4, this.$store.state.height * 0.65) / 2 - 110,
      padAngle: 2 * Math.PI / 200,
      matrix:[],
      ccData:[],
      hoverChord:{
        source:null,
        target:null
      },
      hoverCCGroup:null,
      hoverShopGroup:null
      
     }
  },
  mounted(){
    
    d3.select("#svgC")
    .append("g")
    .attr("id",'Cgroups')
    .attr("transform","translate("+[this.width/2,this.height/2]+")")
    
    d3.select("#svgC")
    .append("g")
    .attr("id",'Cchords')
    .attr("transform","translate("+[this.width/2,this.height/2]+")")
    
    d3.select("#svgC")
    .append("g")
    .attr("id",'Clabels')
    .attr("transform","translate("+[this.width/2,this.height/2]+")")
    
    
    
    
    d3.csv("cc_data.csv")
    .then(d=>{
      this.ccData = d
    })
  },
  computed:{
    hoverShop(){
      return this.$store.state.hoverShop
    },
    matrixStr(){
      let sol = ''
      for(let i = 0;i<this.ccShowData.length;i++){
        for(let j = 0;j<this.ccShowData.length;j++){
          sol += this.ccShowData[i][j]
        }
      }
      return sol
    },
    shopList(){
      let shopList = []
      for(let i = 0;i<this.ccData.length;i++){
        if(this.ccList.indexOf(this.ccData[i].last4ccnum) != -1 && shopList.indexOf(this.ccData[i].location)==-1){
          shopList.push(this.ccData[i].location)
        }
      }
      return shopList
    },
    ccShowData(){
      var getSum = (cclast4,shop)=>{
        let tmp = 0
        for(let i = 0;i<this.ccData.length;i++){
          if(this.ccData[i].last4ccnum == cclast4 && this.ccData[i].location == shop){
            //tmp += parseFloat(this.ccData[i].price)
            tmp+=1
          }
        }
        return tmp
      }
      let sol = []
      for(let i = 0;i<this.ccList.length;i++){
        let tmpary = []
        for(let j = 0;j<this.ccList.length;j++){
          tmpary.push(0)
        }
        for(let j = 0;j<this.shopList.length;j++){
          tmpary.push(getSum(this.ccList[i],this.shopList[j]))
        }
        sol.push(tmpary)
      }
      for(let i = 0;i<this.shopList.length;i++){
        let tmpary = []
        for(let j = 0;j<this.ccList.length;j++){
          tmpary.push(getSum(this.ccList[j],this.shopList[i]))
        }
        for(let j = 0;j<this.shopList.length;j++){
          tmpary.push(0)
        }
        sol.push(tmpary)
      }
      return sol

    },
    ccList(){
      return this.$store.state.viewC.showCCList
    },
    chords(){
      return d3.chord()
              .padAngle(this.padAngle)(this.matrix)
    },
    style(){
      return "position:absolute;border-radius: 15px;left:20px;border:3px solid #37af70;width:"+this.width+"px;"+"height:"+this.height+ "px;" + "top:"+(this.$store.state.height*0.35+40)+"px"
    }
  },
  methods:{
    removeTip(){
      d3.select("#svgC").select("#Ctip").remove()
    },
    createShopTips(d){
      let xy = [event.offsetX,event.offsetY]
      if(xy[0]>= this.width/2){
        xy[0] = 0
      }
      else{
        xy[0] = this.width - 250
      }
      
      if(xy[1]>this.height/2){
        xy[1] = 0
      }
      else{
        xy[1] = this.height - 110
      }
      
      let g = d3.select("#svgC")
      .append("g")
      .attr("id","Ctip")
      .attr("transform","translate("+[xy[0],xy[1]]+")")
      
      g.append("rect")
      .attr("fill",'black')
      .attr("fill-opacity",0.7)
      .attr("height",110)
      .attr("width",250)
      .attr("rx",15)
      
      g.append("text")
      .attr("dx",10)
      .attr("dy",30)
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("Location:")
      
      g.append("text")
      .attr("dx",240)
      .attr("dy",50)
      .attr("text-anchor",'end')
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text(this.shopList[d.index-this.ccList.length])
      
      g.append("text")
      .attr("dx",10)
      .attr("dy",70)
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("Times:")
      
      g.append("text")
      .attr("dx",240)
      .attr("dy",90)
      .attr("text-anchor",'end')
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text(d.value)
    },
    createCCTips(d){
      let xy = [event.offsetX,event.offsetY]
      if(xy[0]>= this.width/2){
        xy[0] = 0
      }
      else{
        xy[0] = this.width - 150
      }
      
      if(xy[1]>this.height/2){
        xy[1] = 0
      }
      else{
        xy[1] = this.height - 110
      }
      
      let g = d3.select("#svgC")
      .append("g")
      .attr("id","Ctip")
      .attr("transform","translate("+[xy[0],xy[1]]+")")
      
      g.append("rect")
      .attr("fill",'black')
      .attr("fill-opacity",0.7)
      .attr("height",110)
      .attr("width",150)
      .attr("rx",15)
      
      g.append("text")
      .attr("dx",10)
      .attr("dy",30)
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("Credit card id:")
      
      g.append("text")
      .attr("dx",140)
      .attr("dy",50)
      .attr("text-anchor",'end')
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text(this.ccList[d.index])
      
      g.append("text")
      .attr("dx",10)
      .attr("dy",70)
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("Times:")
      
      g.append("text")
      .attr("dx",140)
      .attr("dy",90)
      .attr("text-anchor",'end')
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text(d.value)
    },
    createChordTips(d){
      let xy = [event.offsetX,event.offsetY]
      if(xy[0]>= this.width/2){
        xy[0] = 0
      }
      else{
        xy[0] = this.width - 250
      }
      
      if(xy[1]>this.height/2){
        xy[1] = 0
      }
      else{
        xy[1] = this.height - 150
      }
      let g = d3.select("#svgC")
      .append("g")
      .attr("id",'Ctip')
      .attr("transform","translate("+[xy[0],xy[1]]+")")
      
      g.append("rect")
      .attr("fill",'black')
      .attr("fill-opacity",0.7)
      .attr("height",150)
      .attr("width",250)
      .attr("rx",15)
      
      g.append("text")
      .attr("dx",10)
      .attr("dy",30)
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("Credit card id:")
      
      g.append("text")
      .attr("dx",240)
      .attr("dy",50)
      .attr("text-anchor",'end')
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text(this.ccList[d.source.index])
      
      g.append("text")
      .attr("dx",10)
      .attr("dy",70)
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("Target location:")
      
      g.append("text")
      .attr("dx",240)
      .attr("dy",90)
      .attr("text-anchor",'end')
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text(this.shopList[d.target.index-this.ccList.length])
      
      g.append("text")
      .attr("dx",10)
      .attr("dy",110)
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text("Times:")
      
      g.append("text")
      .attr("dx",240)
      .attr("dy",130)
      .attr("text-anchor",'end')
      .attr("fill",'white')
      .attr("font-size",15)
      .attr("font-weight",'bold')
      .text(d.source.value)
      
      
      
    },
    createCircle(){
      let endAngle = 2 * Math.PI
      var data = [
          {startAngle: 0 * endAngle, endAngle: endAngle },
      ];
      
      var arc = d4.svg.arc().outerRadius(this.width * 2).innerRadius(0);
      let svg = d4.select("#svgC")
      svg.select("g").select("#transition").remove();
      
      svg.append("g").attr("id",'transition')
          .attr("transform", "translate("+[this.width/2,this.height/2]+")")
          .selectAll("path.arc")
              .data(data)
              .enter()
              .append("path")
              .attr("class", "arc")
              .attr("fill",'white')
              .attr('d',arc)
              
              
          svg.selectAll(".arc")
          .data(data)
          .transition()
          .duration(2000)
          
          .attrTween("d", function (d) {
              var start = {startAngle: endAngle, endAngle: endAngle}; // <-A
              var interpolate = d4.interpolate(d, start); // <-B
              return function (t) {
                  return arc(interpolate(t)); // <-C
              };
          })
          .remove()
    },
    removeCircle(){
      
      let endAngle = 2 * Math.PI
      var data = [
          {startAngle: 0 * endAngle, endAngle: endAngle },
         
      ];
      
      var arc = d4.svg.arc().outerRadius(this.width).innerRadius(0);
      let svg = d3.select("#svgC")
      
       svg.select("g").select("#transition").remove();
      
      svg.append("g").attr("id",'transition')
          .attr("transform","translate("+[this.width/2,this.height/2]+")")
          .selectAll(".arc")
              .data(data)
              .enter()
              .append("path")
              .attr("class", "arc")
              .attr("fill","white")
              .attr('d',arc)
             
              .transition().duration(2000)
              
              .attrTween("d", function (d) { 
                  var start = {startAngle: endAngle, endAngle: endAngle}; // <-A
                  var interpolate = d3.interpolate(start, d); // <-B
                  return function (t) {
                      return arc(interpolate(t)); // <-C
                  };
              })
              .remove()
              
              
              
          
          
    },
    testUpdate(){
      this.matrix = this.ccShowData
      this.drawGroups()
      this.drawChords()
      this.drawTicks()
      this.drawLabels()
      
    },
    drawGroups(){
        const groups = this.chords.groups // 节点数组
        
        const arc = d3.arc()
            .innerRadius(this.innerR)
            .outerRadius(this.outerR)
        console.log(groups)
        let g = d3.select("#Cgroups")
                  
           g.selectAll('path')
            .data(groups)
            .enter()
            .append('g')
            .attr('class', 'group')
            .append("path")
            .on("click",d=>{
              if(d.index<this.ccList.length){
                this.$store.commit({
                  type:'updateStream',
                  ccLast4:this.ccList[d.index]
                })
              }
              
            })
            .on("mouseover",d=>{
              
              if(d.index<this.ccList.length){
                this.createCCTips(d)
                this.hoverCCGroup = d.index
                d3.select("#Cgroups")
                .selectAll("path")
                .transition()
                .attr("stroke",'none')
                .attr("stroke-width",5)
                
                d3.select("#Cgroups")
                .selectAll("path")
                .filter(d=>d.index == this.hoverCCGroup)
                .transition()
                .attr("stroke",'#fa857e')
                .attr("stroke-width",5)
                
                d3.select("#Cchords")
                .selectAll("path")
                .transition()
                .attr("fill",d=>this.colors[d.source.index % 12])
                .attr("fill-opacity",0.2)
                .attr("stroke-width",0)
                
                d3.select("#Cchords")
                .selectAll("path")
                .filter(d=>d.source.index == this.hoverCCGroup)
                .transition()
                
                .attr("fill-opacity",0.9)
                .attr("stroke-width",1)
                
              }
              else{
                this.createShopTips(d)
                this.hoverShopGroup = d.index
                d3.select("#Cgroups")
                .selectAll("path")
                .transition()
                .attr("stroke",'none')
                .attr("stroke-width",5)
                
                d3.select("#Cgroups")
                .selectAll("path")
                .filter(d=>d.index == this.hoverShopGroup)
                .transition()
                .attr("stroke",'#0bbdf1')
                .attr("stroke-width",5)
                
                d3.select("#Cchords")
                .selectAll("path")
                .transition()
                .attr("fill",d=>this.colors[d.source.index % 12])
                .attr("fill-opacity",0.2)
                .attr("stroke-width",0)
                
                d3.select("#Cchords")
                .selectAll("path")
                .filter(d=>d.target.index == this.hoverShopGroup)
                .transition()
                
                .attr("fill-opacity",0.9)
                .attr("stroke-width",1)
                
              }
            })
            .on("mousemove",d=>{
              this.removeTip()
              if(d.index<this.ccList.length){
                this.createCCTips(d)
              }
              else{
                this.createShopTips(d)
              }
              
            })
            .on("mouseout",()=>{
              this.removeTip()
              this.hoverCCGroup = null
              this.hoverShopGroup = null
              d3.select("#Cgroups")
              .selectAll("path")
              .transition()
              .attr("stroke",'none')
              
              d3.select("#Cchords")
              .selectAll("path")
              .transition()
              .attr("fill",d=>this.colors[d.source.index % 12])
              .attr("fill-opacity",0.5)
              .attr("stroke-width",1)
              
              
            })

           g.selectAll('path')
            .data(groups)
            .transition()
            .duration(1000)
            .attr('d', d => {
                return arc(d)
            })
            .attr('fill', (_, i) => this.colors[i%12])
            
           g.selectAll('path')
            .data(groups)
            .exit()
            .remove()
            
           
           
           g.selectAll('.group')
            .data(groups)
            .exit()
            .remove()
          
    },
    drawTicks() {
        d3.select("#svgC").selectAll(".ticks").remove()
        
        d3.selectAll('.group')
            .append('g')
            .attr('class', 'ticks')
            .selectAll('line')
            .data(d => {
                const {
                    value,
                    startAngle,
                    endAngle
                } = d
                const k = (endAngle - startAngle) / value
        
                return d3.range(0, value, 3).map(value => ({
                    text: value,
                    angle: d.startAngle + value * k,
                }))
            })
            .enter()
            .append('g')
            .call(this.createRadialTicks)
    
    },
    drawChords() {
        const ribbon = d3.ribbon()
            .radius(this.innerR - 20)
        let g = d3.select("#Cchords")
      
           
           g.selectAll('path')
            .data(this.chords)
            .enter()
            .append('path')
            .attr('class', 'chord')
            .attr("fill","none")
            .on("click",d=>{
              this.$store.commit({
                type:'updateStream',
                ccLast4:this.ccList[d.source.index]
              })
            })
            .on("mouseover",(d)=>{
              
              this.createChordTips(d)
              this.hoverChord.source = d.source.index
              this.hoverChord.target = d.target.index
              
              g.selectAll("path")
              .transition()
              .attr("fill",d=>this.colors[d.source.index % 12])
              .attr("fill-opacity",0.3)
              .attr("stroke-width",0)
              
              g.selectAll('path')
              .filter(d=>this.hoverChord.source == d.source.index
              &&this.hoverChord.target == d.target.index)
              .transition()
              .attr("fill",'#37af70')
              .attr("fill-opacity",0.8)
              
              
              d3.select("#Cgroups")
              .selectAll("path")
              .filter(d=>d.index == this.hoverChord.source)
              .transition()
              .attr("stroke",'#fa857e')
              .attr("stroke-width",5)
              
              d3.select("#Cgroups")
              .selectAll("path")
              .filter(d=>d.index == this.hoverChord.target)
              .transition()
              .attr("stroke",'#0bbdf1')
              .attr("stroke-width",5)
              
              
              
              
            })
            .on("mousemove",d=>{
              this.removeTip()
              this.createChordTips(d)
            })
            .on("mouseout",()=>{
              this.removeTip()
              this.hoverChord.source = null
              this.hoverChord.target = null
              g.selectAll("path")
              .transition()
              .attr("fill",d=>this.colors[d.source.index % 12])
              .attr("fill-opacity",0.5)
              .attr("stroke-width",1)
              d3.select("#Cgroups")
              .selectAll("path")
              .transition()
              .attr("stroke",'none')
            })
            
           g.selectAll("path")
            .data(this.chords)
            .filter(d=>d.source.index <=this.ccList.length)
            .transition()
            .duration(1000)
            .attr('d', ribbon)
            .attr("fill",d=>this.colors[d.source.index % 12])
            .attr("fill-opacity",0.5)
            .attr("stroke",d=>d3.rgb(this.colors[d.source.index % 12]).darker())
            
           
      
            g.selectAll("path")
             .data(this.chords)
             .exit()
             .remove()
    },
    
    drawLabels() {
        var getFirst = (str) => {
          if(str){
            for(let i = 0;i<str.length;i++){
              if (str.substring(i,i+1) == ' '){
                return str.substring(0,i)
              }
            }
            return str
          }
        }
        
        var rotateAngle =(d)=>{
            return this.tsRadian2angle( (d.startAngle + d.endAngle) / 2 )
        }
        //d3.select("#Clabels").selectAll(".labels").remove()
        let g = d3.select("#Clabels")
       
           g.selectAll('text')
            .data(this.chords.groups)
            .enter()
            .append('g')
            .attr("class","labels")
            .style("font-size",0)
            .style("fill","black")
            .attr("transform",d=>{
              const deg = rotateAngle(d)
              return "rotate("+(deg-90)+")translate("+[this.outerR+30,0]+")"
            })
            .append('text')
            .attr('dominant-baseline', 'middle')
            
            .text(d => {
             if(d.index<this.ccList.length){
                return 'CC ID: ' + this.ccList[d.index]
              }
              else if (d.index >= this.ccList.length){
               let k = this.shopList[d.index - this.ccList.length]
               return getFirst(k)
               //return k
                
                
              }
              
            })
            
            .attr("transform",function(d){
              const deg = rotateAngle(d)
              return deg>180?"rotate("+(180)+")translate("+[-this.getBBox().width,0]+")":""
            })
            
           g.selectAll('.labels')
            
            .data(this.chords.groups)
            .style("font-size",13)
            .style("fill","black")
            .transition()
            .duration(1000)
            .attr("transform",d=>{
              const deg = rotateAngle(d)
              return "rotate("+(deg-90)+")translate("+[this.outerR+30,0]+")"
            })
            
            g.selectAll("text")
            .attr("transform",function(d){
              const deg = rotateAngle(d)
              return deg>180?"rotate("+(180)+")translate("+[-this.getBBox().width,0]+")":""
            })
            .text(d => {
             if(d.index<this.ccList.length){
                return 'CC ID: ' + this.ccList[d.index]
              }
              else if (d.index >= this.ccList.length){
                let k = this.shopList[d.index - this.ccList.length]
                return getFirst(k)
                //return k
                
                
              }
              
            })
            .attr("transform",function(d){
              const deg = rotateAngle(d)
              return deg>180?"rotate("+(180)+")translate("+[-this.getBBox().width,0]+")":""
            })
           
           g.selectAll(".labels")
           .data(this.chords.groups)
           .exit()
           .remove()
           
           g.selectAll("text")
           .data(this.chords.groups)
           .exit()
           .remove()
            
            
    },
    rotateAngle(d){
        return this.tsRadian2angle( (d.startAngle + d.endAngle) / 2 )
    },
    setAttr(ele, attrs){
        for (const [key, val] of Object.entries(attrs)) {
            ele.attr(key, val)
        }
    },
    tsRadian2angle(radian){
        return radian * 180 / Math.PI
    },
    createRadialTicks(tick){
        tick.call(this.setAttr, {
            transform: d => {
                const deg = this.tsRadian2angle(d.angle)
                let outerR = this.outerR
                
                return `
                    rotate(${ deg - 90 })
                    translate(${ outerR }, 1)
                `
            },
            'text-anchor': 'start',
            'dominant-baseline': 'middle',
            'stroke-width': 1,
            'font-size': 10,
            'font-weight': 100,
        })
        .call(g => {
            g.append('line')
                .attr('stroke', '#000')
                .attr('x2', 6)
    
            g.append('text')
                .text(d => d.text)
                .attr('transform', 'translate(8, 0)')
        })
    }
        
    
  },
  watch:{
    hoverShop(nv){
      if(nv){
        d3.select("#Cgroups")
        .selectAll("path")
        .transition()
        .attr("stroke",'none')
        .attr("stroke-width",5)
        
        d3.select("#Cgroups")
        .selectAll("path")
        .filter(d=>this.shopList[d.index-this.ccList.length] == this.hoverShop)
        .transition()
        .attr("stroke",'#0bbdf1')
        .attr("stroke-width",5)
        
        d3.select("#Cchords")
        .selectAll("path")
        .transition()
        .attr("fill",d=>this.colors[d.source.index % 12])
        .attr("fill-opacity",0.2)
        .attr("stroke-width",0)
        
        d3.select("#Cchords")
        .selectAll("path")
        .filter(d=>this.shopList[d.target.index-this.ccList.length] == this.hoverShop)
        .transition()
        
        .attr("fill-opacity",0.9)
        .attr("stroke-width",1)
      }
      else if (nv == null){
        d3.select("#Cgroups")
        .selectAll("path")
        .transition()
        .attr("stroke",'none')
        
        d3.select("#Cchords")
        .selectAll("path")
        .transition()
        .attr("fill",d=>this.colors[d.source.index % 12])
        .attr("fill-opacity",0.5)
        .attr("stroke-width",1)
      }
    },
    matrixStr(nv,ov){
      if(nv && ov == ''){
        this.testUpdate()
        this.createCircle()
      }
      else if(ov && nv == ''){
        this.removeCircle()
        
        d3.select("#svgC").selectAll(".group").transition().duration(1500).remove()
        d3.select("#svgC").selectAll(".chord").transition().duration(1500).remove()
        d3.select("#svgC").selectAll(".labels").transition().duration(1500).remove()
        
      }
      else{
        this.testUpdate()
      }
      
    }
  }
}
</script>

