import { createStore } from 'vuex'

export default createStore({
  state: {
    width:document.documentElement.clientWidth-60,
    height:document.documentElement.clientHeight-60,
    idList:[],
    matchList:[],
    day:'01/06/2014',
    viewB:{
      showIDList:[],
      showTime:[0,24]
    },
    viewC:{
      showCCList:[]
    },
    viewE:{
      ccLast4:0
    },
    startTime:{
      hour:0,
      minute:0,
      second:0
    },
    endTime:{
      hour:23,
      minute:59,
      second:59
    },
    interval:1000,
    hoverShop:null
  },  
  mutations: {
    changeHoverShop(state,payload){
      state.hoverShop = payload.shop
    },
    timeSet(state,payload){
      state.startTime = {
        hour:payload.sh,
        minute:payload.sm,
        second:payload.ss
      }
      
      state.endTime = {
        hour:payload.eh,
        minute:payload.em,
        second:payload.es
      }
      
      state.interval = payload.interval
      
    },
    changeDay(state,payload){
      state.day = payload.day
    },
    judge(state){
      if(state.viewC.showCCList.indexOf(state.viewE.ccLast4) == -1){
        state.viewE.ccLast4 = 0
      }
    },
    log(state,payload){
      
      //console.log(state.matchList)
      if(payload.tp1 == 'add' && state.viewB.showIDList.length<=2 && state.viewB.showIDList.indexOf(payload.id) ==-1
      && state.idList.indexOf(payload.id) != -1){
        state.viewB.showIDList.push(payload.id)
        console.log("!!!")
      }
      else if(payload.tp1 == 'del'){
        let sol = []
        let strID = payload.id.toString()
        
        for(let i = 0;i<state.viewB.showIDList.length;i++){
          
          if(state.viewB.showIDList[i] != strID){
            
            sol.push(state.viewB.showIDList[i])
            
          }
        }
        state.viewB.showIDList = sol
      }
      
      let sol2 = []
      for(let i = 0;i<state.viewB.showIDList.length;i++){
        for(let j = 0;j<state.matchList.length;j++){
          if(state.viewB.showIDList[i] == state.matchList[j]['id']){
            sol2.push(state.matchList[j]['cclast4'])
            if(state.matchList[j]['sus1'] != '0'){
              sol2.push(state.matchList[j]['sus1'])
            }
            
            if(state.matchList[j]['sus2'] != '0'){
              sol2.push(state.matchList[j]['sus2'])
            }
          }
        }
      }
      state.viewC.showCCList = sol2
      
      
      
      
      
      
    },
    updateShowIDList(state,payload){
      let sol = []
      for(let i = 0;i<payload.list.length;i++){
        sol.push(payload.list[i])
      }
      state.viewB.showIDList = sol
    },
    updateShowCCList(state,payload){
      let sol = []
      for(let i = 0;i<payload.list.length;i++){
        sol.push(payload.list[i])
      }
      state.viewC.showCCList = sol
    },
    
    updateShowTime(state,payload){
      state.viewB.showTime = payload.time
    },
    updateStream(state,payload){
      state.viewE.ccLast4 = payload.ccLast4
    },
    changeList(state,payload){
      state.idList = payload.IDlist
      state.matchList = payload.match
    }
  },
  actions: {
  },
  modules: {
  }
})
