<template>
    <div id = 'calendar' >
    <svg :style = 'calendarStyle'>
      <text text-anchor = 'middle' 
      dy = 20 :dx = 'width/2' 
      font-size = 18
      font-weight = 'bold'>January , 2014</text>
      <line
      x1 = 0
      :x2 = 'width'
      y1 = 30
      y2 = 30
      stroke = '#37AF70'
      stroke-width = 2></line>
      <g v-for = '(d,i) in calendar'
      :key = 'i'>
      <text 
      font-size = 13
      text-anchor = 'middle'
      :dx = 'i%7 * (width-20)/7 + 20'
      :dy = '50 + Math.floor(i/7) * (height/4-20)/7'
      :fill = '(i<=33 && i>=3)? "black" : "#c0d4c2"'>
        {{d}}
      </text>
      <rect v-if = '(d<=19 && d>=6)'
      cursor = 'pointer'
      :x = 'i%7 * (width-20)/7 + 12'
      :y = '38 + Math.floor(i/7) * (height/4-20)/7'
      :height = '(height/4-20)/7 - 3'
      :width = '(width-20)/7 - 4'
      :fill = 'd==day? "#37AF70" :"white"'
      stroke = '#37AF70'
      rx = 4
      fill-opacity = 0.4
      @click = 'day = d'>
        
      </rect>
      </g>
    </svg>
    </div>
   
    <div :style = 'style'>
      <p id = 'title' >Console</p>
     
      <span> <input class = 'textIn' type = 'text' v-model = 'id' placeholder="Input ID"></span>
      <button :style = 'btnStyle'
      @click = 'changeShowIdList(id,"add")' >submit</button>
      <br/>
      <div class = 'timeConfig'> 
        Advanced Settings<br/>
        Time Rate:<input class = 'timeTextIn' :style = 'intervalColor' type = 'text' v-model = 'interval' placeholder="10-2000">
        <br/><br/>Start time:<br/>
       
        <input class = 'timeTextIn2' :style = '"border:3px solid "+ judge(startHour,"hour")' type = 'text' v-model = 'startHour' placeholder="hour">:
        <input class = 'timeTextIn2' :style = '"border:3px solid "+ judge(startMinute,"ms")' type = 'text' v-model = 'startMinute' placeholder="min">:
        <input class = 'timeTextIn2' :style = '"border:3px solid "+ judge(startSecond,"ms")' type = 'text' v-model = 'startSecond' placeholder="sec">
         <br/><br/>End time:<br/>
         <input class = 'timeTextIn2' :style = '"border:3px solid "+ judge(endHour,"hour")' type = 'text' v-model = 'endHour' placeholder="hour">:
         <input class = 'timeTextIn2' :style = '"border:3px solid "+ judge(endMinute,"ms")' type = 'text' v-model = 'endMinute' placeholder="min">:
         <input class = 'timeTextIn2' :style = '"border:3px solid "+ judge(endSecond,"ms")' type = 'text' v-model = 'endSecond' placeholder="sec">
         <button @click = 'setTime()' :style = 'flagColor' id = 'timeBtn'>Set time</button>
         <!-- {{this.$store.state.startTime}}{{this.$store.state.endTime}}{{this.$store.state.interval}} -->
      </div>
      
      <!-- <p>{{this.$store.state.viewB.showIDList}}</p>
      <p>{{this.$store.state.viewC.showCCList}}</p> -->
      <!-- <span> cc:<input class = 'textIn' type = 'text' v-model = 'cc' ></span>
      <button @click = 'changeShowCCList(cc,"add")'>add</button> -->
      
      
        
      
    </div>
</template>

<script>
  import * as d3 from 'd3'
export default {
	name: 'viewD',
	data(){
    
    
    let idList = ['101','104','105','106','107']
    for(let i = 1;i<=35;i++){
      idList.push(i.toString())
      
    }
    let calendar = [29,30,31]
    for(let i = 1;i<=31;i++){
      calendar.push(i)
    }
    calendar.push(1)
    
    return {
      idList:idList,
      width:this.$store.state.width *0.10,
      height:this.$store.state.height * 0.6,
      //showID:[],
      matchData:[],
      id:'',
      cc:0,
      ccData:[],
      calendar:calendar,
      day:6,
      interval:'',
      startHour:'',
      startMinute:'',
      startSecond:'',
      endHour:'',
      endMinute:'',
      endSecond:'',
     
      
      
      
    }
  },
  mounted(){
    d3.csv('cc_data.csv',d=>{
      this.ccData = d
    })
    d3.csv('match.csv',d=>{
      this.matchData = d
      this.$store.commit({
        type:'changeList',
        IDlist:this.idList,
        match:this.matchData
      })
    })
    
    
   
    
   
    
  },
  computed:{
    intervalColor(){
      if(this.interval == ''){
        return "border:3px solid #37AF70"
      }
      else if(this.intervalFlag){
        return "border:3px solid #37AF70"
      }
      else{
        return "border:3px solid red"
      }
    },
    intervalFlag(){
      if(Math.floor(this.interval) == this.interval && this.interval>=10 && this.interval<=2000){
        return true
      }
      else{
        return false
      }
    },
    flagColor(){
      if(this.setTimeFlag){
        return "background-color:#37AF70"
      }
      else{
        return "background-color:#a39f99"
      }
    },
    setTimeFlag(){
      let sh = parseInt(this.startHour)
      let sm = parseInt(this.startMinute)
      let ss = parseInt(this.startSecond)
      let eh = parseInt(this.endHour)
      let em = parseInt(this.endMinute)
      let es = parseInt(this.endSecond)
      
      if(this.intervalFlag && this.judge(sh,"hour") == '#37AF70' && this.judge(eh,"hour") == '#37AF70'&&
      this.judge(sm,"ms")== '#37AF70' && this.judge(em,"ms")== '#37AF70' &&
      this.judge(ss,"ms") == '#37AF70'&& this.judge(ss,"ms")== '#37AF70' &&
      sh * 3600 + sm * 60 + ss <eh * 3600 + em * 60 + es
      ){
        return true
      }
     
      return false
      
    },
    calendarStyle(){
      return "position:absolute;top:20px;border-radius: 10px;border:3px solid #37af70;width:"+(this.width)+"px;"+"height:"+(this.height/4 - 10)+ "px;" + "left:"+(this.$store.state.width*0.4+40)+"px"
    },
    btnStyle(){
      if(this.idList.indexOf(this.id) == -1 || this.showID.indexOf(this.id) != -1 || this.showID.length == 3){
        return "transition: all 0.5s;cursor:pointer;border-radius: 10px;border: 0px solid #37AF70;background-color:#a39f99;width:"+this.width/3+"px;height:"+26+"px;color:white;font-size:16px"
      }
      else{
        return "transition: all 0.5s;cursor:pointer;border-radius: 10px;border: 0px solid #37AF70;background-color:#37AF70;width:"+this.width/3+"px;height:"+26+"px;color:white;font-size:16px"
      }
    },
    showID(){
      return this.$store.state.viewB.showIDList
    },
    showCC(){
      let sol = []
      for(let i = 0;i<this.showID.length;i++){
        for(let j = 0;j<this.matchData.length;j++){
          if(this.showID[i] == this.matchData[j]['id']){
            sol.push(this.matchData[j]['cclast4'])
            if(this.matchData[j]['sus1'] != '0'){
              sol.push(this.matchData[j]['sus1'])
            }
            
            if(this.matchData[j]['sus2'] != '0'){
              sol.push(this.matchData[j]['sus2'])
            }
          }
        }
      }
      
      /* for(let i = 0;i<this.$store.state.viewB.showIDList.length;i++){
        for(let j = 0;j<this.matchData.length;j++){
          if(this.$store.state.viewB.showIDList[i] == this.matchData[j]['id']){
            sol.push(this.matchData[j]['cclast4'])
            if(this.matchData[j]['sus1'] != '0'){
              sol.push(this.matchData[j]['sus1'])
            }
            
            if(this.matchData[j]['sus2'] != '0'){
              sol.push(this.matchData[j]['sus2'])
            }
          }
        }
      } */
      return sol
    },
    ccList(){
      let sol = []
      for(let i = 0;i<this.ccData.length;i++){
        if(sol.indexOf(this.ccData[i].last4ccnum) == -1){
          sol.push(this.ccData[i].last4ccnum)
        }
      }
      return sol
    },
    showID1(){
      return this.showID.length
    },
    showCC1(){
      return this.showCC.length
    },
    style(){
      return "position:absolute;border-radius: 15px;top:"+(this.height/4 + 20)+"px;border:3px solid #37af70;width:"+this.width+"px;"+"height:"+(this.height * 3/4)+ "px;" + "left:"+(this.$store.state.width*0.4+40)+"px"
    },
  },
  methods:{
    setTime(){
      if(this.setTimeFlag){
        this.$store.commit({
          type:'timeSet',
          sh:parseInt(this.startHour),
          sm:parseInt(this.startMinute),
          ss:parseInt(this.startSecond),
          eh:parseInt(this.endHour),
          em:parseInt(this.endMinute),
          es:parseInt(this.endSecond),
          interval:parseInt(this.interval)
        })
        
        this.interval=''
        this.startHour=''
        this.startMinute=''
        this.startSecond=''
        this.endHour=''
        this.endMinute=''
        this.endSecond=''
      }
      else{
        alert("Invalid!!! You may try reset your advanced settings. \n\n输入错误！！请尝试重新输入高级时间设置。\n\nダメ！！詳細設定を再入力してみてください。")
      }
    },
    judge(d,type){
      let v = type=="hour"? 24:60
      if(d>=0 && d<v && Math.floor(d) == d){
        return '#37AF70'
      }
      else{
        return "red"
      }
    },
    changeShowIdList(id,tp){
      if(this.idList.indexOf(this.id) == -1){
        alert("Invalid!!! You may text id range in [1,35] or [104,107] or 101. \n \n输入错误!!! 可以输入的id范围是[1,35]或[104,107]或101。\n \nダメダメ！！！入力可能なid範囲は[1,35]と[104,107]と101である。")
        
      }
      else if(this.showID.indexOf(this.id) != -1){
        alert("Invalid!!! Inputting ID which has been announced is not allowed.\n \n输入错误!!!不允许添加已经添加过的id。\n \nだめよ～！！追加したidの追加は許可されていません。")
      }
      else if(this.showID.length == 3){
        alert("Warning!!! This system only supports showing up to three id at the same time. Please delete any of them first.\n\n警告!!!这个系统目前只支持同时展示3个id的数据，请先删除其中的某些id。\n\nだめなのよ〜 >_<!!このシステムは最大3つのidのデータを同時に表示することをサポートしています。まず、その中のいくつかのidを削除してください。")
      }
      
      else{
        
        this.$store.commit({
          type:'log',
          tp1:tp,
          id:id
        })
        let cc 
        this.id = ''
        for(let i = 0;i<this.matchData.length;i++){
          
          
          if(this.matchData[i].id == id){
            cc = this.matchData[i].cclast4
            
          }
        }
        
        this.$store.commit({
          type:'updateStream',
          ccLast4:cc
        })
      }

    },
   
  },
  watch:{
    day(nv){
      let sol
      if(nv <10){
        sol = "01/0"+nv+"/2014"
      }
      else if (nv>=10){
        sol = "01/"+nv+"/2014"
      }
      this.$store.commit({
        type:'changeDay',
        day:sol
      })
    }
   
  }
}
</script>

